import React, { useMemo } from 'react'

import { useConfiguration } from '../../api/internal'
import ApiBrowser from '../../components/ApiBrowser'
import { useActiveConfigurationName } from '../../services/ActiveConfiguration'

const DirectoryApiDocs = () => {
  const { data } = useConfiguration()

  const { configurationName } = useActiveConfigurationName()
  const configFields = useMemo(() => {
    return data?.configs.find((e) => e.name === configurationName)
  }, [configurationName, data?.configs])

  const directoryServiceUrl = configFields?.directoryServiceUrl
  const directoryApiKey = configFields?.directoryApiKey
  const directoryTenantId = configFields?.directoryTenantId

  return (
    <ApiBrowser
      apiKeys={[
        {
          key: 'DirectoryAPIKey',
          value: directoryApiKey ? `Basic ${directoryApiKey}` : '',
        },
        {
          key: 'TenantID',
          value: directoryTenantId || '',
        },
      ]}
      openApiUrl={`${directoryServiceUrl}/directory/openapi.json`}
    ></ApiBrowser>
  )
}

export default DirectoryApiDocs
